  flex-basis: 50%;
  height: 101px;
  flex-grow: 1;
  flex-shrink: 1;
}
.way__strip--tr {
  border-left: 2px dashed #a5a5a5;
  border-bottom: 2px dashed #a5a5a5;
  border-radius: 0 0 0 90px;
  align-self: flex-start;
}
.way__strip--lb {
  border-top: 2px dashed #a5a5a5;
  border-right: 2px dashed #a5a5a5;
  border-radius: 0 90px 0 0;
  align-self: flex-end;
}
.way__strip--tl {
  border-right: 2px dashed #a5a5a5;
  border-bottom: 2px dashed #a5a5a5;
  border-radius: 0 0 90px 0;
  align-self: flex-start;
}
.way__strip--rb {
  border-top: 2px dashed #a5a5a5;
  border-left: 2px dashed #a5a5a5;
  border-radius: 90px 0 0 0;
  align-self: flex-end;
}
.way__strip--half {
  width: 50%;
  flex-shrink: 0;
}
.way__control {
  width: 100%;
}
@media (min-width: 980px) {
  .way {
    padding-left: 40px;
    padding-right: 40px;
  }
  .way--tablet {
    overflow: hidden;
  }
  .way--lb .way__content,
  .way--rb .way__content {
    flex-wrap: nowrap;
    justify-content: space-between;
  }
  .way--start .way__strips {
    width: calc(100% - 80px);
  }
  .way--control .way__content:after {
    top: 50px;
  }
  .way--freedom .way__content:after {
    top: 32px;
  }
  .way--start .way__content {
    padding-bottom: 320px;
  }
  .ie10 .way__control,
  .ie11 .way__control {
    flex-basis: 100%;
    flex-shrink: 1;
    text-align: right;
  }
}
@media (min-width: 1900px) {
  .way {
    padding-left: 120px;
    padding-right: 120px;
  }
  .way__content {
    justify-content: space-between;
  }
  .way--start .way__strips {
    width: calc(100% - 240px);
  }
}
.wd-slider {
  width: 430px;
  margin: auto;
  overflow: hidden;
  position: relative;
}
.wd-slider__slides {
  width: 300%;
  display: flex;
  align-items: center;
  transition: transform 0.3s ease-out;
  justify-content: space-between;
  transform: translateX(0);
  margin-bottom: 93px;
}
.wd-slider--two .wd-slider__slides {
  transform: translateX(-66.66%);
}
.wd-slider__slide {
  width: 33.33%;
  text-align: center;
}
.wd-slider__car {
  width: 100%;
}
.wd-slider__slide--one .wd-slider__car {
  width: 430px;
}
.wd-slider__slide--two .wd-slider__car {
  width: 336px;
}
.wd-slider__cards {
  width: 300%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transform: translateX(-66.66%);
  transition: transform 0.3s ease-out;
  position: absolute;
  bottom: 35px;
  left: 0;
}
.wd-slider--two .wd-slider__cards {
  transform: translateX(0);
}
.wd-slider__card-wrapper {
  width: 33.33%;
}
.wd-slider__card {
  width: 390px;
  height: 110px;
  position: relative;
  margin: 0 auto;
  display: flex;
  align-items: stretch;
  background-color: rgba(255,255,255,0.98);
  border: 5px solid #ededed;
  border-radius: 55px 5px 5px 55px;
}
.wd-slider__face {
  width: 110px;
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  top: -5px;
  left: -5px;
}
.wd-slider__img {
  width: 100%;
  border-radius: 50%;
}
.wd-slider__title {
  padding-left: 135px;
  display: flex;
  align-items: center;
}
.wd-slider__text {
  color: #36374c;
  line-height: 1.25em;
}
.wd-slider__pagination {
  display: flex;
  justify-content: center;
}
.wd-slider__pag {
  width: 16px;
  height: 16px;
  border: 2px solid #00a3af;
  border-radius: 50%;
  background-color: #fff;
}
.wd-slider__pag:not(:last-child) {
  margin-right: 5px;
}
.wd-slider__pag--active {
  background-color: #00a3af;
}
@media (min-width: 980px) {
  .wd-slider {
    width: 480px;
  }
  .wd-slider__slides {
    margin-bottom: 115px;
  }
  .wd-slider__slide--one .wd-slider__car {
    width: 480px;
  }
  .wd-slider__slide--two .wd-slider__car {
    width: 360px;
  }
  .wd-slider__card {
    height: 140px;
    width: 416px;
    border-radius: 70px 5px 5px 70px;
  }
  .wd-slider__face {
    width: 140px;
  }
  .wd-slider__title {
    padding-left: 160px;
  }
}
@media (min-width: 1900px) {
  .wd-slider {
    width: 660px;
  }
  .wd-slider__slides {
    margin-bottom: 110px;
  }
  .wd-slider__slide--one .wd-slider__car {
    width: 660px;
  }
  .wd-slider__slide--two .wd-slider__car {
    width: 490px;
  }
}
.ya-map {
  flex-grow: 1;
  height: 450px;
}
.ya-map__icon {
  transform: translate(-7px, -40px);
}
.ya-map__icon--red {
  background-size: 40px 40px;
